'use strict';

exports.name = 'addClassesToSVGElement';
exports.type = 'visitor';
exports.active = false;
exports.description = 'adds classnames to an outer <svg> element';
var ENOCLS = "Error in plugin \"addClassesToSVGElement\": absent parameters.\nIt should have a list of classes in \"classNames\" or one \"className\".\nConfig example:\n\nplugins: [\n  {\n    name: \"addClassesToSVGElement\",\n    params: {\n      className: \"mySvg\"\n    }\n  }\n]\n\nplugins: [\n  {\n    name: \"addClassesToSVGElement\",\n    params: {\n      classNames: [\"mySvg\", \"size-big\"]\n    }\n  }\n]\n";
/**
 * Add classnames to an outer <svg> element. Example config:
 *
 * plugins: [
 *   {
 *     name: "addClassesToSVGElement",
 *     params: {
 *       className: "mySvg"
 *     }
 *   }
 * ]
 *
 * plugins: [
 *   {
 *     name: "addClassesToSVGElement",
 *     params: {
 *       classNames: ["mySvg", "size-big"]
 *     }
 *   }
 * ]
 *
 * @author April Arcus
 *
 * @type {import('../lib/types').Plugin<{
 *   className?: string,
 *   classNames?: Array<string>
 * }>}
 */

exports.fn = function (root, params) {
  if (!(Array.isArray(params.classNames) && params.classNames.some(String)) && !params.className) {
    console.error(ENOCLS);
    return null;
  }

  var classNames = params.classNames || [params.className];
  return {
    element: {
      enter: function enter(node, parentNode) {
        if (node.name === 'svg' && parentNode.type === 'root') {
          var classList = new Set(node.attributes.class == null ? null : node.attributes.class.split(' '));
          var _iteratorNormalCompletion = true;
          var _didIteratorError = false;
          var _iteratorError = undefined;

          try {
            for (var _iterator = classNames[Symbol.iterator](), _step; !(_iteratorNormalCompletion = (_step = _iterator.next()).done); _iteratorNormalCompletion = true) {
              var className = _step.value;

              if (className != null) {
                classList.add(className);
              }
            }
          } catch (err) {
            _didIteratorError = true;
            _iteratorError = err;
          } finally {
            try {
              if (!_iteratorNormalCompletion && _iterator.return != null) {
                _iterator.return();
              }
            } finally {
              if (_didIteratorError) {
                throw _iteratorError;
              }
            }
          }

          node.attributes.class = Array.from(classList).join(' ');
        }
      }
    }
  };
};